import React, { useEffect, useState } from "react";
import ArticlesList from "../components/ArticlesList";
import CommentsList from "../components/CommentsList";
import UpvotesSection from "../components/UpvotesSection";
import AddCommentForm from "../components/AddCommentForm";
import ArticlesContent from "./article-content";
import NotFoundPage from "./NotFoundPage";

const ArticlePage = ({ match }) => {
  const name = match.params.name;
  const article = ArticlesContent.find((article) => article.name === name);
  const otherArticles = ArticlesContent.filter(
    (article) => article.name !== name
  );

  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      console.log(body);
      setArticleInfo(body);
    };
    fetchData();
  }, [name]);

  if (!article) return <NotFoundPage />;

  return (
    <>
      <h1>{article.title}</h1>
      <UpvotesSection
        articleName={name}
        upvotes={articleInfo.upvotes}
        setArticleInfo={setArticleInfo}
      />

      {article.content.map((paragraph, index) => (
        <p key={index}>{paragraph}</p>
      ))}

      <CommentsList comments={articleInfo.comments}></CommentsList>
      <AddCommentForm
        articleName={name}
        setArticleInfo={setArticleInfo}
      ></AddCommentForm>
      <h3> other articles</h3>
      <ArticlesList articles={otherArticles} />
    </>
  );
};

export default ArticlePage;
